body {
  padding: 1em;
}

.TradeMachineTitle {
  font-size: 3rem;
}

.TradeCols {
  display: flex;
  /* flex-direction: column; */
  width: 100%;
}

.TradeCol1 {
  width: 100%;
  margin: 1em 0em;
  padding: 1em;
  border-radius: 5px;
  border: 1px solid #ccc
}

.TradeCol2 {
  width: 100%;
  margin: 1em 0em 1em 1em;
  padding: 1em;
  border-radius: 5px;
  border: 1px solid #ccc
}


@media only screen and (max-width: 900px) {
  .TradeCols {
    flex-direction: column;
  }

  .TradeCol1 {
    width: auto;
  }

  .TradeCol2 {
    width: auto;
    margin: 1em 0em;
  }
}


@media only screen and (max-width: 400px) {
  .TradeMachineTitle {
    font-size: 2rem;
  }
}
