.CircleAvatar {
    background-color: #e8f0fe;
    height: 50px;
    width: 50px;
    border-radius: 50px;
}

.CircleAvatar > h1 {
    height: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    color: #3b7dd9;
}

@media only screen and (max-width: 1300px) {
    .CircleAvatar {
        height: 40px;
        width: 40px;
    }

    .CircleAvatar > h1 {
        height: 40px;
        width: 40px;
        font-size: 1.2em;
    }
}