.ErrorBanner {
    background-color: #fedbda;
}

.ErrorHeader {
    color: #c00000;
}

.SuccessBanner {
    background-color: #d2ecc9;
}

.SuccessHeader {
    color: #167423;
}

.ErrorCloseBtn {
    color: #c00000;
}

.SuccessCloseBtn {
    color: #167423;
}

.ErrorBanner, .SuccessBanner {
    padding: 0em 1em;
    border-radius: 5px;
    display: grid;
    grid-template-columns: 10% 80% 10%;
}

.ErrorHeader, .SuccessHeader {
    align-self: center;
    justify-self: center;
}

.ErrorCloseBtn, .SuccessCloseBtn {
    cursor: pointer;
    align-self: center;
    width: max-content;
}

@media only screen and (max-width: 900px) {
    .ErrorHeader, .SuccessHeader {
      font-size: 1.5rem;
    }
}

@media only screen and (max-width: 400px) {
    .ErrorHeader, .SuccessHeader {
      font-size: 1rem;
    }

    .ErrorCloseBtn > span, .SuccessCloseBtn > span {
        font-size: 1rem;
        justify-self: center;
    }
}