.TitleBar {
    display: flex;
    align-items: center;
    justify-content: center;
}

.TitleBar > p {
    margin: 1em;
    color: grey;
}

.Accordion {
    /* border-bottom: 2px solid #ccc; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 1em 0em;
}

@media only screen and (max-width: 400px) {
    .TitleBar > h2 {
        font-size: 1.25rem;
    }

    .TitleBar > p {
        font-size: 0.8rem;
    }
}

@media only screen and (max-width: 400px) {
    .TitleBar > h2 {
        font-size: 1rem;
    }

    .TitleBar > p {
        font-size: 0.7rem;
    }
}