.Player {
    background-color: white;
    display: grid;
    grid-template-columns: 2.5% 10% 75% 10% 2.5%;
    padding: 0.5em 0em;
    align-items: center;
    border-radius: 5px;
    width: 100%;
    border: 1px solid #ccc;
    margin: 0.25em;
}

.CircleAvatar {
    grid-column: 2/3;
    justify-self: start;
}

.InputText {
    grid-column: 3/4;
    height: 40px;
    font-size: 1.2rem;
    border: none;
    font-family: 'Roboto';
    outline: none;
    transition: 0.5s ease;
    border-bottom: 2px solid #ddd;
}

.InputText:focus {
    border-bottom: 2px solid #3b7dd9;
}

.DeleteButton {
    grid-column: 4/5;
    width: max-content;
    justify-self: end;
    cursor: pointer;
    color: #c00000;
    transition: 0.5s ease;
}

.DeleteButton:hover {
    color: #c00000;
}

.DeleteButtonSmall {
    visibility: hidden;
}

@media only screen and (max-width: 1300px) {
    .DeleteButton > span{
        font-size: 1.25rem;
    }
}

@media only screen and (max-width: 1050px) {
    .Player {
        grid-template-columns: 2.5% 10% 77.5% 7.5% 1.5%;
    }
}

@media only screen and (max-width: 1000px) {
    .Player {
        grid-template-columns: 1.5% 10% 2.5% 75% 7.5% 1.5%;
    }

    .InputText {
        grid-column: 4/5;
    }

    .DeleteButton {
        grid-column: 6/7;
    }
}

@media only screen and (max-width: 900px) {
    .Player {
        grid-template-columns: 2.5% 10% 75% 10% 2.5%;
    }

    .InputText {
        grid-column: 3/4;
    }

    .DeleteButton {
        grid-column: 4/5;
    }
}

@media only screen and (max-width: 500px) {
    .Player {
        grid-template-columns: 1.5% 10% 1.5% 75% 8% 2.5%;
    }

    .InputText {
        grid-column: 4/5;
        font-size: 1rem;
    }

    .DeleteButton{
        grid-column: 5/6;
    }
}

@media only screen and (max-width: 450px) {
    .Player {
        grid-template-columns: 1.5% 10% 2.5% 82.5% 1.5%
    }

    .InputText {
        grid-column: 4/5;
        font-size: 0.9rem;
    }

    .DeleteButton{
        visibility: hidden;
    }

    .DeleteButtonSmall {
        visibility: visible;
        grid-column: 4/5;
        justify-self: end;
    }

    .DeleteButtonSmall > span {
        font-size: 1rem;
        color: #3b7dd9;
        cursor: pointer;
    }
}

@media only screen and (max-width: 400px) {
    .Player {
        grid-template-columns: 1.5% 10% 5% 80%;
    }

    .InputText {
        grid-column: 4/5;
    }

    .DeleteButton{
        visibility: hidden;
    }
}

@media only screen and (max-width: 350px) {
    .Player {
        grid-template-columns: 1.5% 10% 7.5% 77.5%;
    }

    .InputText {
        grid-column: 4/5;
    }

    .DeleteButton{
        visibility: hidden;
    }
}