.TradeButton {
    background-color: #d2ecc9;
    color: #167423;
    border: none;
    border-radius: 5px;
    padding: 1em;
    cursor: pointer;
    font-weight: bold;
    width: 100%;
    /* margin: 1em; */
    font-size: 1rem;
    font-family: 'Roboto';
}

.TradeButton:disabled {
    cursor: not-allowed;
    background-color: #E0E0E0;
    color: #757575;
}